<template>
    <el-dialog title='' :visible.sync="dialogForm" class="updatePwd">

        <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
                <el-input
                        v-model.trim="form.url"
                        type="textarea"
                        :rows="2"
                        placeholder=", ','">
                </el-input>
                <div @click="formatUrlsStr()" class="geshi"></div>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.purpose" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in purpose"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.wx" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in wx"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="qq:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.qq" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in qq"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.status" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.parsing_status" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in parsing_status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.group_num" >
                    <el-option value="" label=""></el-option>
                    <el-option
                            v-for="(item, $key) in group_num"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false"> </el-button>
            <el-button type="primary" @click="submit()"></el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .geshi{
        border:1px solid #ddd;
        border-radius:4px;
        height:40px;
        line-height:40px;
        text-align:center;
        margin-top:20px;
        cursor:pointer;
    }
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'
    import ElOption from "../../../node_modules/element-ui/packages/select/src/option";

    const quRequest = new QuRequest()

    export default {
        components: {ElOption},
        data() {
            return {
                group_num:[
                    { 'name': '', 'value': '0'},
                    { 'name': '1','value': '1'},
                    { 'name': '2', 'value': '2'},
                    { 'name': '3', 'value': '3'},
                    { 'name': '4','value': '4'},
                    { 'name': '5', 'value': '5'},
                    { 'name': '6', 'value': '6'},
                    { 'name': '7', 'value': '7'},
                    { 'name': '8','value': '8'},
                    { 'name': '9', 'value': '9'},
                    { 'name': '10', 'value': '10'},
                ],
                status:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                    { 'name': '', 'value': '2'},
                ],
                wx:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                ],
                qq:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                ],
                purpose:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                    { 'name': '', 'value': '2'},
                    { 'name': '', 'value': '3'},
                    { 'name': 'QQ','value': '4'},
                    { 'name': '', 'value': '5'},
                    { 'name': '', 'value': '6'},
                    { 'name': '', 'value': '7'},
                    { 'name': '','value': '8'},
                    { 'name': '', 'value': '9'},
                ],

                parsing_status:[
                    { 'name': '', 'value': '1'},
                    { 'name': '', 'value': '0'},
                ],
                radio:1,
                dialogForm: false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    id:'',
                    url: '',
                    note:'',
                    purpose:'',
                    status:'',
                    wx:'',
                    qq:'',
                    parsing_status:'',
                    group_num:'',
                }
            }
        },
        methods: {

            formatUrlsStr: function() {
                this.form.url = this.form.url.replace(/(.+)(?:\n)|(?:\r\n)/g, '$1,');
            },

            showDomainDialog(item) {
                if(item){
                    this.isShow = false;
                    this.form.urls = '';
                    this.form.purpose = '';
                    this.form.wx = '';
                    this.form.qq = '';
                    this.form.status = '';
                    this.form.group_num = '';
                    this.form.parsing_status = '';
                }

                this.dialogForm = true;
            },
            submit(){
                quRequest.send(quRequest.Apis.qukan.domain.batchModify,{
                    urls: this.form.url,
                    data: JSON.stringify(this.form)
                    }
                ).then(res => {
                    this.$message.success("");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`${ err.message || '' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


