<template>
    <el-dialog  title='' :visible.sync="dialogForm" class="updatePwd">

        <el-form :model="form">
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model.trim="form.purpose" >
                    <el-option
                            v-for="(item, $key) in purpose"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="url" :label-width="formLabelWidth">
                <el-input
                        v-model.trim="form.url"
                        type="textarea"
                        :rows="2"
                        placeholder=", ','">
                </el-input>
                <div @click="formatUrlsStr()" class="geshi"></div>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false"> </el-button>
            <el-button type="primary" @click="submit()"></el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .geshi{
        border:1px solid #ddd;
        border-radius:4px;
        height:40px;
        line-height:40px;
        text-align:center;
        margin-top:20px;
        cursor:pointer;
    }
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {

                purpose:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                    { 'name': '', 'value': '2'},
                    { 'name': '', 'value': '3'},
                    { 'name': 'QQ','value': '4'},
                    { 'name': '', 'value': '5'},
                    { 'name': '', 'value': '6'},
                    { 'name': '', 'value': '7'},
                    { 'name': '','value': '8'},
                    { 'name': '', 'value': '9'},
                ],
                dialogForm:false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    url: '',
                    purpose:'0',
                }
            }
        },
        methods: {

            showInfoDialog() {
                this.dialogForm = true;
            },
            formatUrlsStr: function() {
                this.form.url = this.form.url.replace(/(.+)(?:\n)|(?:\r\n)/g, '$1,');
            },
            submit(){
                quRequest.send((quRequest.Apis.qukan.domain.domainImport),this.form
                ).then(res => {
                    this.$message.success("");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`${ err.message || '' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


