<template>
    <el-dialog :title='radioShow==true?"":""' :visible.sync="dialogForm" class="updatePwd">

        <el-form :model="form">

            <el-form-item label=":" :label-width="formLabelWidth">
                <el-input v-model="form.url" auto-complete="off" :disabled="!radioShow"></el-input>
            </el-form-item>

            <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select
                            size="small"
                            v-model="form.purpose" >
                        <el-option
                                v-for="(item, $key) in purpose"
                                :value="item.value"
                                :label="item.name"
                                :key="$key">
                        </el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.wx" >
                    <el-option
                            v-for="(item, $key) in wx"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="qq:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.qq" >
                    <el-option
                            v-for="(item, $key) in qq"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.status" >
                    <el-option
                            v-for="(item, $key) in status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.parsing_status" >
                    <el-option
                            v-for="(item, $key) in parsing_status"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.group_num" >
                    <el-option
                            v-for="(item, $key) in group_num"
                            :value="item.value"
                            :label="item.name"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <div class="radio-list">
                <el-form-item label="use_qq:" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="form.use_qq" label="0"></el-radio>
                    <el-radio class="radio" v-model="form.use_qq" label="1"></el-radio>
                </el-form-item>
            </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false"> </el-button>
            <el-button type="primary" @click="submit()"></el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                group_num:[
                    { 'name': '', 'value': '0'},
                    { 'name': '1','value': '1'},
                    { 'name': '2', 'value': '2'},
                    { 'name': '3', 'value': '3'},
                    { 'name': '4','value': '4'},
                    { 'name': '5', 'value': '5'},
                    { 'name': '6', 'value': '6'},
                    { 'name': '7', 'value': '7'},
                    { 'name': '8','value': '8'},
                    { 'name': '9', 'value': '9'},
                    { 'name': '10', 'value': '10'},
                ],
                status:[
                    { 'name': '','value': '0'},
                    { 'name': '', 'value': '2'},
                    { 'name': '', 'value': '1'},
                ],
                wx:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                ],
                qq:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                ],
                purpose:[
                    { 'name': '', 'value': '0'},
                    { 'name': '','value': '1'},
                    { 'name': '', 'value': '2'},
                    { 'name': '', 'value': '3'},
                    { 'name': 'QQ','value': '4'},
                    { 'name': '', 'value': '5'},
                    { 'name': '', 'value': '6'},
                    { 'name': '', 'value': '7'},
                    { 'name': '','value': '8'},
                    { 'name': '', 'value': '9'},
                ],

                parsing_status:[
                    { 'name': '', 'value': '1'},
                    { 'name': '', 'value': '0'},
                ],
                radio:1,
                dialogForm: false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    id:'',
                    url: '',
                    note:'',
                    purpose:'0',
                    status:'0',
                    wx:'0',
                    qq:'0',
                    parsing_status:'0',
                    use_qq:'0',
                    group_num:'0',
                }
            }
        },
        methods: {

            showInfoDialog(item) {
                if(item){
                    this.isShow = false;
                    this.form.id = item.id;
                    this.form.url = item.url;
                    this.form.purpose = item.purpose;
                    this.form.wx = item.wx;
                    this.form.qq = item.qq;
                    this.form.status = item.status;
                    this.form.group_num = item.group_num;
                    this.form.parsing_status = item.parsing_status;
                    this.form.use_qq = item.use_qq;
                    this.radioShow = false;
                }else{
                    this.form.purpose = '0';
                    this.form.status = '0';
                    this.form.wx = '0';
                    this.form.qq = '0';
                    this.form.url ='';
                    this.form.parsing_status = '0';
                    this.form.use_qq = '0';
                    this.form.group_num = '0';
                    this.form.note = '';
                    this.radioShow = true;
                }

                this.dialogForm = true;
            },
            submit(){
                quRequest.send((this.radioShow == true?quRequest.Apis.qukan.domain.add:quRequest.Apis.qukan.domain.modify),this.form
                ).then(res => {
                    this.$message.success("");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`${ err.message || '' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


